.report-header-section {
    display: flex; /* Enables flexbox */
    justify-content: space-between; /* This pushes the first item (H2) to the left and the last item (Button) to the right */
    align-items: center; /* Vertically aligns items in the middle */
    margin-bottom: var(--spacing-lg); /* Adds some space below this section */
}

.report-header-section h2 {
    margin: 0; /* Remove default margin from H2 to ensure clean alignment */
}

.report-page-container {
    padding: var(--spacing-xl);
    background-color: var(--color-background-light);
    min-height: calc(100vh - var(--header-height) - var(--footer-height));
    color: var(--color-text-primary);
}

.report-page-container h2 {
    font-size: var(--font-size-h2);
    color: var(--color-primary-dark);
    margin-bottom: var(--spacing-lg);
    text-align: center;
}

.report-controls {
    display: flex;
    flex-wrap: wrap;
    gap: var(--spacing-md);
    margin-bottom: var(--spacing-xl);
    justify-content: center;
    align-items: center;
    background-color: var(--color-white);
    padding: var(--spacing-md);
    border-radius: var(--border-radius-medium);
    box-shadow: var(--shadow-small);
}

.report-controls label {
    font-weight: bold;
    margin-right: var(--spacing-sm);
    color: var(--color-text-secondary);
}

.report-controls select {
    padding: var(--spacing-xs) var(--spacing-sm);
    border: 1px solid var(--color-medium-gray);
    border-radius: var(--border-radius-small);
    font-size: var(--font-size-base);
    min-width: 150px;
    background-color: var(--color-white);
    cursor: pointer;
    transition: border-color var(--transition-ease);
}

.report-controls select:focus {
    outline: none;
    border-color: var(--color-secondary);
    box-shadow: 0 0 0 2px rgba(var(--color-secondary-rgb), 0.2); /* Assuming --color-secondary-rgb is defined in variables.css */
}

/* Use your Button component styles if you switch to buttons for grouping */
.report-controls .button {
    min-width: 120px;
}

.report-content {
    background-color: var(--color-white);
    padding: var(--spacing-lg);
    border-radius: var(--border-radius-medium);
    box-shadow: var(--shadow-medium);
    margin-top: var(--spacing-xl);
}

.report-status {
    text-align: center;
    font-size: var(--font-size-large);
    padding: var(--spacing-lg);
    color: var(--color-text-secondary);
}

.report-error {
    text-align: center;
    font-size: var(--font-size-large);
    padding: var(--spacing-lg);
    color: var(--color-error);
    background-color: rgba(var(--color-error-rgb), 0.1);
    border: 1px solid var(--color-error);
    border-radius: var(--border-radius-medium);
}

.no-data-message {
    text-align: center;
    padding: var(--spacing-lg);
    color: var(--color-text-secondary);
    font-style: italic;
